import React from 'react'
import Layout from '../Layout/Layout'
import { makeStyles } from '@mui/styles'
import { Typography } from '@mui/material'
import { Link } from 'react-router-dom'
import Markdown from 'react-markdown'
import test from '../Contents/testing.MD'

const useStyles = makeStyles({
  root: {
    marginTop: '60px',
    width: '100%',
    minHeight: '100vh',
    marginBottom: '0',
    padding: '24px',
    boxSizing: 'border-box'
  }
})

const lists = [
  {
    id: 'event-loop-in-javascript',
    title: 'Event Loop trong JavaScript - Cch JavaScript x l bt ng b',
    author: 'thong.nguyen van',
    date: 'September 04, 2023'
  },
  {
    id: 'index-sql-part1',
    title: 'Ch mc (Index) trong SQL - Part 1',
    author: 'thong.nguyen van',
    date: 'September 09, 2023'
  }
]

interface VideoProps {
  src?: string
}

const components = {
  video: ({ src }: VideoProps) => <video src={src} />
}

function Home () {
  const classes = useStyles()

  return (
    <Layout>
      <div className={classes.root}>
        <Typography variant='h5' gutterBottom>
          Hi.
        </Typography>
        <Typography variant='caption' display='block' gutterBottom>
          Cho mng cc bn n vi 1 Blog ph bi, c mc um tm ca mnh. Mnh
          to ra trang Blog ny vi mc ch chnh l  hc tp v chia s, hy
          vng cc bn s tm kim c 1 iu g   y. Mnh l Thng, cu
          sinh vin CNTT Bch Khoa H Ni (K63), hin ang l mt k s phn
          mm, rt vui c cc bn gh thm.
        </Typography>
        <Typography
          variant='caption'
          display='block'
          gutterBottom
          sx={{ marginTop: '24px' }}
        >
          Tht ra y ton l nhng th m nhiu ngi  vit, nhng mnh vn
          mun vit li ( n tp li hoc trong lc t hc mt th mi m g
          ) mt cch d hiu hn cho cc bn mi bt u lp trnh c th tham
          kho, cc ch  ca mnh thng th s xoay quanh JavaScript, Golang,
          mt cht devOps v vi ch  khc, d nhin l u  mc c bn thi,
          mi ngi c v cho mnh xin gp   chng ta cng ci thin, mnh
          cm n.
        </Typography>

        <hr style={{ marginTop: '40px' }} />

        {lists.length &&
          lists.map((post, i) => {
            return (
              <div key={i} className='post-card'>
                <h2>
                  <Link className='links' to={`/blog/${post.id}`}>
                    {post.title}
                  </Link>
                </h2>
                <Typography
                  variant='caption'
                  display='block'
                  gutterBottom
                  sx={{ color: '#2b003ds' }}
                >
                  Published on {post.date} by {post.author}
                </Typography>
                <hr />
                {/* <Markdown components={components}>text</Markdown> */}
                {/* <small><Link className="links" to={`/post/${post.id}`}>Read more</Link></small> */}
              </div>
            )
          })}
      </div>
    </Layout>
  )
}

export default Home
